@-webkit-keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(359deg);
    }
}
.connect-div{
    position: relative;
    z-index: 1;
}

.connect{
    position: fixed;
    display: inline-block;
    bottom:40px;
    left:60px;

}
.connect a{
    color:white;
}
