    font-size: 24px !important;
}
.fa-github-square{
    font-size: 29px !important;
}
.connect i:hover{
    color:#69E781;
    cursor:pointer;
}

.down{
    font-size: 30px;
    position: absolute;
    top:92vh;
    left: 48%;
    width:50px;
    height: 50px;
    /*border:2px dashed #ffffff;*/
    border-radius: 50%;
    /*box-shadow: 0 0 0 5px #fff, 0 5px 25px rgba(0,0,0,0.8);*/
    overflow: hidden;
}

.down .fa{
    color:#69E781;
    font-size: 60px;
    position: absolute;
    top:-20%;
    left: 11%;
    -webkit-animation: animate 1s linear infinite;
            animation: animate 1s linear infinite;
}

.dot{
    position: relative;
    left: 8px;
}

@-webkit-keyframes animate{
    0%{
        top:-15%
    }
    /*40%{*/
        /*top:60%*/
    /*}*/
    50%{
        top:-5%
    }
    /*80%{*/
        /*top:60%*/
    /*}*/
    100%{
        top:5%
    }

}

@keyframes animate{
    0%{
        top:-15%
    }
    /*40%{*/
        /*top:60%*/
    /*}*/
    50%{
        top:-5%
    }
    /*80%{*/
        /*top:60%*/
    /*}*/
    100%{
        top:5%
    }

}



@media screen and (min-width: 800px) {
    .logo{
        position:absolute;
        bottom:0;
        width:200px;
    }
    .logo-div{
        /*background-color: #69E781;*/
        min-height: 80vh;
        position:relative;
    }
}

@media screen and (max-width: 480px) {
    .intro-div h1{
        font-size:26px;
    }
    .intro-div h2{
        font-size:23px;
    }
}
