.custom-link, .custom-link:visited {
    text-decoration: none;
    position: relative;
    color: #69E781;
}
.custom-link:after, .custom-link:visited:after{
    content: '';
    height: 2px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    background: #69E781;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
.custom-link:hover:after, .custom-link:visited:hover:after {
    width: 100%;
}

.navbar-nav .active{
    color: #69E781 !important;
}
